#modal {
    position: fixed;
    top: 0;
    right: 20vw;
    left: 20vw;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

#modal-background {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
}

#modal-content {
    position: absolute;
    background-color: #D9D9D9;
    border-radius: 12px;
    width: 100%;
    max-width: 920px;
    min-width: 840px;
    padding: 40px;
}
